class NavBar extends HTMLElement {
  constructor() {
    super();
    this.shadowDOM = this.attachShadow({ mode: "open" });
  }

  connectedCallback() {
    this.render();
  }

  render() {
    this.shadowDOM.innerHTML = `
      <style>
      .navbar {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        height: 50px;
        padding: 10px;
        background: rgba(200, 158, 60, 0.9);
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        backdrop-filter: blur(2.5px);
        -webkit-backdrop-filter: blur(2.5px);
        z-index: 100;
      }
      
      .navbar-logo h1 {
        display: flex;
        position: absolute;
        align-items: center;
        font-size: 30px;
        margin-left: 60px;
        margin-top: 15px;
        color: rgba(0, 0, 0, 1);
      }
      
      .navbar-menu {
        display: flex;
        gap: 20px;
      }
      
      .navbar-menu li {
         list-style: none;
      }

      .navbar-menu li a {
        font-size: 1.2rem;
        color: rgba(0, 0, 0, 0.8);
        font-weight: bold;
        text-decoration: none;
        padding: 10px;
        border-radius: 5px;
        transition: all 0.3s ease-in-out;
      }
      
      .navbar-menu li a:hover {
        background: rgba(182, 167, 141, 0.7);
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        backdrop-filter: blur(4px);
        -webkit-backdrop-filter: blur(5.3px);
        z-index: 1;
      }

      .navbar-hamburger {
        display: none;
        background-color: transparent;
        border: none;
        cursor: pointer;
        color: rgba(0, 0, 0, 0.8);
        font-size: 24px;
      }

      .navbar-hamburger span{
        display: block;
        width: 20px;
        height: 2px;
        margin: 4px 0;
        background-color: #333;
      }

      .navbar-hamburger:hover span {
        background-color: #555;
      }

      @media (max-width: 428px) {
        .navbar-logo h2 a {
          font-size: 1rem;
        }
        .navbar-logo a {
          padding: 5%;
        }
        .navbar-logo h1 {
          font-size: 20px;
          margin-left: 65px;
          margin-top: 20px;
        }
        .navbar-menu {
          gap: 2px;
        }
        .navbar-menu a {
          font-size: 0.9rem;
        }
      }

      @media (max-width: 320px) {
        .navbar-logo h2 a {
          font-size: 0.8rem;
        }
        .navbar-logo a {
          padding: 5%;
        }
        .navbar-logo h1 {
          font-size: 15px;
          margin-left: 60px;
          margin-top: 25px;
        }
        .navbar-menu {
          gap: 2px;
        }
        .navbar-menu a {
          font-size: 0.7rem;
        }
      }


      @media (max-width: 280px) {
        .navbar-logo a {
          width: 10px;
          height: 10px;
          padding: 5%;
        }
        .navbar-logo h1 {
          font-size: 15px;
        }
        .navbar-menu {
          gap: 2px;
          flex-direction: column;
        }
        .navbar-menu a {
          font-size: 0.8rem;
        }
      }
      
      @media (max-width: 768px) {
      .navbar-menu {
        display: none;
      }

      .navbar-hamburger {
        display: block;
      }

      .navbar-hamburger span {
        display: block;
        width: 25px;
        height: 3px;
        background-color: #333;
        margin-bottom: 5px;
      }

      .navbar-hamburger span:last-child {
        margin-bottom: 0;
      }
    
      .nav-menu.show {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 70px;
        left: 0;
        width: 100%;
        background-color: #f2f2f2;
        padding: 20px;
        z-index: 1;
      }
    
      .nav-menu.show li {
        margin-right: 0;
        margin-bottom: 10px;
      }
    
      .nav-menu.show li:last-child {
        margin-bottom: 0;
      }
    
      .nav-menu.show a {
        color: #333;
        font-weight: bold;
        transition: all 0.2s ease;
      }
    
      .nav-menu.show a:hover {
        color: #f44336;
      }

    }
      </style>
      <nav id="home" class="navbar">
      <div class="navbar-logo">
        <h1>MenuMate</h1>
        <a href="#"><img src="https://img.icons8.com/glyph-neue/64/null/meal.png" alt="Logo Aplikasi"></a>
      </div>

      <button class="navbar-hamburger" aria-label="Toggle nav" aria aria-expanded="false" aria aria-controls="navbar-menu">
        <span></span>
        <span></span>
        <span></span>
      </button>
    
      <ul class="navbar-menu">
        <li><a href="#home">Home</a></li>
        <li><a href="#favorite">Favorite</a></li>
        <li><a href="#about">About Us</a></li>
      </ul> 
    </nav>
    `;
    const navbarHamburger = document.querySelector(".navbar-hamburger");
    const navbarMenu = document.querySelector(".navbar-menu");

    navbarHamburger.addEventListener("click", function () {
      navbarMenu.classList.toggle("show");
    });
  }
}

customElements.define("nav-bar", NavBar);
