const data = require("../data/DATA.json");

class RestoItem extends HTMLElement {
  constructor() {
    super();

    const shadowRoot = this.attachShadow({ mode: "open" });
    const style = document.createElement("style");
    style.textContent = `
    :host {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 20px;
      margin: 80px;
    }
    
    ul {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
      grid-gap: 20px;
      list-style: none;
      margin: 0;
      padding: 0;
    }
    
    li {
      width: auto;
      height: auto;
      background: rgba(255, 255, 248, 0.8);
      border-radius: 16px;
      box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
      padding: 20px;
    }
    
    img {
        width:100%;
        height:350px; 
        object-fit:cover;
        border-radius:5px;
    }
    
    h3 {
        color: rgba(0, 0, 0, 0.9);
        margin-top: 1rem;
        font-size:2rem;
        font-family: 'Quicksand', sans-serif;
        font-weight:600;
        text-align:center;
    }
    
    .description {
        margin:1.5rem;
        line-height:1.2;
    }
    
    .description p {
        color:rgba(0, 0, 0, 0.9);
        margin-top: 1rem;
        font-size:.9em; 
        text-align: justify;
        overflow:hidden; 
        display:-webkit-box; 
        -webkit-line-clamp:5; 
                line-clamp:5; 
        -webkit-box-orient:vertical; 
    }

    hr {
      width: 100%;
      height: 2px;
      border: none;
      background: rgba(124,79,15,1);
    }
    
    .info {
        display:flex; 
        flex-direction:row; 
        justify-content: flex-end; 
        align-items: center; 
        margin: auto;
    }
    
    .info p {
        color: rgba(0, 0, 0, 0.9); 
        font-family: 'Quicksand', serif; 
        font-size:.9em; 
        margin-right: 1rem;
    }
    
    .city {
      display: inline-block;
      margin-right: 5px;
      width: 25px;
      height: 25px;
      background: url('https://img.icons8.com/pastel-glyph/64/null/marker--v1.png') center/contain no-repeat;
    }

    .city p {
      margin-bottom: 20px;
    }

    .rating {
      display: inline-block;
      margin-right: 5px;
      width: 25px;
      height: 25px;
      background: url('https://img.icons8.com/retro/32/null/filled-star.png') center/contain no-repeat;
    }

    @media only screen and (min-width: 1280px) {
      ul {
          grid-template-columns: repeat(3, 1fr);
      }
    }
    @media only screen and (max-width: 728px) {
      :host {
        margin: 0px;
      }
    }
      <div id="favorite"
      ></div>
    `;
    shadowRoot.appendChild(style);

    const restoWrapper = document.createElement("div");
    restoWrapper.setAttribute("class", "resto-item");

    const restoList = document.createElement("ul");
    restoList.setAttribute("class", "resto-list");

    data.restaurants.forEach((restaurant) => {
      const listItem = document.createElement("li");
      listItem.innerHTML = `
      <img src="${restaurant.pictureId}" alt="${restaurant.name}">
      <h3>${restaurant.name}</h3>
      <div class="description">
        <p>${restaurant.description}</p>
      </div>
      <hr>
      <div class="info">
        <p><i class="city"></i>${restaurant.city}</p>
        <p><i class="rating"></i>${restaurant.rating}</p>
      </div>
      `;
      restoList.appendChild(listItem);
    });

    restoWrapper.appendChild(restoList);
    shadowRoot.appendChild(restoWrapper);
  }
}

customElements.define("resto-item", RestoItem);
